import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AppComponent } from './app.component';
import { FavoriteComponent } from './favorite/favorite.component';
import { LikeComponent } from './like/like.component';
import { DirectivesComponent } from './directives/directives.component';
import { NgforComponent } from './ngfor/ngfor.component';
import { TrackbyComponent } from './trackby/trackby.component';
import { InputFormatDirective } from './input-format.directive';
import { CustomDrctvComponent } from './custom-drctv/custom-drctv.component';
import { InputFormat2Directive } from './input-format2.directive';
import { ZippyComponent } from './zippy/zippy.component';
import { HeaderComponent } from './header/header.component';
import { SvgDasharrayComponent } from './svg-dasharray/svg-dasharray.component';
import { XhrSandboxComponent } from './xhr-sandbox/xhr-sandbox.component';
import { FetchApiSandboxComponent } from './fetch-api-sandbox/fetch-api-sandbox.component';
import { MaterializeModule } from 'angular2-materialize';


@NgModule({
  declarations: [
    HeaderComponent,
    AppComponent,
    FavoriteComponent,
    LikeComponent,
    DirectivesComponent,
    NgforComponent,
    TrackbyComponent,
    InputFormatDirective,
    CustomDrctvComponent,
    InputFormat2Directive,
    ZippyComponent,
    HeaderComponent,
    SvgDasharrayComponent,
    XhrSandboxComponent,
    FetchApiSandboxComponent
  ],
  imports: [BrowserModule, NgbModule.forRoot(), MaterializeModule],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



