import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'fetch-api-sandbox',
  templateUrl: './fetch-api-sandbox.component.html',
  styleUrls: ['./fetch-api-sandbox.component.css']
})
export class FetchApiSandboxComponent implements OnInit {

  constructor() { }

  ngOnInit() {

    document.getElementById('getText').addEventListener('click', getText);
    document.getElementById('getJson').addEventListener('click', getJson);
    document.getElementById('getApi').addEventListener('click', getApi);
    document.getElementById('addPost').addEventListener('submit', addPost);


    function getText() {
      // fetch('sample.txt')
      // .then(function (res) {
      //     return res.text();
      // })
      // .then(function (data) {
      //     console.log(data);
      // })

      fetch('../../assets/sample.txt')
        .then((res) => res.text())
        .then((data) => {
          document.getElementById('output').innerHTML = '<h4>Text File</h4>' + data;
        });
    }


    function getJson() {
      fetch('../../assets/sample.json')
        .then((res) => res.json())
        .then((data) => {

          let output = '<h4>JSON File</h4>';
          data.forEach(function (user) {
            output += `
                    <ul>
                    <li>ID: ${user.id}</li>
                    <li>Name: ${user.name}</li>
                    <li>Email: ${user.email}</li>
                    </ul>
                    `;
          });
          document.getElementById('output').innerHTML = output;
        });
    }

    function getApi() {
      fetch('https://jsonplaceholder.typicode.com/posts')
        .then(response => response.json())
        .then(json => {

          let output = '<h4>API Posts </h4>';
          json.forEach(function (posts) {
            output += `
                <h5>${posts.title}</h5>
                <p>${posts.body}</p>
                <br><br>
                `;
          });
          document.getElementById('output').innerHTML = output;
        });
    }

    function addPost(e) {
      e.preventDefault();

      const title = (<HTMLInputElement>document.getElementById('title')).value;
      const body = (<HTMLInputElement>document.getElementById('body')).value;


      fetch('https://jsonplaceholder.typicode.com/posts', {
        method: 'POST',
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Content-type': 'application/json'
        },
        body: JSON.stringify({ title: title, body: body })
      })
        .then((res) => res.json())
        .then((data) => console.log(data));
    }



  }

}



