import { Component, OnInit } from '@angular/core';
import { log } from 'util';

@Component({
  selector: 'favorite',
  templateUrl: './favorite.component.html',
  styleUrls: ['./favorite.component.css']
})
export class FavoriteComponent {
  isFavorite: boolean;

  onClick() {
    this.isFavorite = !this.isFavorite;
  }
}



