import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'xhr-sandbox',
  templateUrl: './xhr-sandbox.component.html',
  styleUrls: ['./xhr-sandbox.component.css']
})
export class XhrSandboxComponent implements OnInit {


  constructor() { }

  ngOnInit() {

    document.getElementById('getText').addEventListener('click', getText);
    document.getElementById('getJson').addEventListener('click', getJson);
    document.getElementById('getApi').addEventListener('click', getApi);



    function getText() {
      const xhr = new XMLHttpRequest;
      xhr.open('GET', '../../assets/sample.txt', true);
      xhr.onload = function () {
        if (xhr.status === 200) {
          document.getElementById('output').innerHTML = xhr.responseText;
        }
      };
      xhr.send();
    }



    function getJson() {
      const xhr = new XMLHttpRequest;
      xhr.open('GET', '../../assets/sample.json', true);
      xhr.onload = function () {
        if (xhr.status === 200) {
          const jsonResp = JSON.parse(xhr.response);
          let output = '';
          for (const i of Object.keys(jsonResp)) {
            output += `
                    <div>ID: ${jsonResp[i].id}</div>
                    <div>Name: ${jsonResp[i].name}</div>
                    <div>Email: ${jsonResp[i].email}</div>
                    <br><br>
                    `;
          }
          document.getElementById('output').innerHTML = output;
        }
      };
      xhr.send();
    }



    function getApi() {
      const xhr = new XMLHttpRequest;
      xhr.open('GET', 'https://api.github.com/users', true);
      xhr.onload = function () {
        if (xhr.status === 200) {
          const apiResp = JSON.parse(xhr.response);
          let output = '';
          for (const i of Object.keys(apiResp)) {
            output += `
                    <ul>
                    <img src="${apiResp[i].avatar_url}" style="height:70px;width:70px;">
                    <li>Name: ${apiResp[i].id}</li>
                    <li>Email: ${apiResp[i].login}</li>
                    </ul>
                    <br><br>
                    `;

          }
          document.getElementById('output').innerHTML = output;
        }
      };

      xhr.send();
    }



  }

}



